.App {
  margin:0 auto;
}
a{
  text-decoration:none;
  color:inherit;
}
.container{
  margin:0 50px 0 50px;
}
/* 배너 */
.header{
  display:flex;
  width:100%;
  height:150px;
  align-items:center;
}
.header img{
  width:220px;
  height:50px;
  object-fit:cover;
}
.search{
  width:580px;
  height:55px;
  border:2px solid #03cf5d;
  display:flex;
}
.search input{
  width:80%;
  height:35px;
  margin-top:8px;
  font-size:18px;  
  padding-left:10px;
  border:none;
  outline:none;
}
.search img{
  width:30px;
  height:55px;
  object-fit:none;
}
.searchbtn{
  width:8%;
  background-color:#03cf5d;
}

.main{
  background:#444;
}
.footer{
  background:#555;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
